export default {
	data() {
		return {
			useUser:this.user,
			url:this.url1
		}
	},
	onLoad() {
	   
	},
	onShow(){
		
	},
	methods: {
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		
		    // 修改昵称
		xgnc : function(){
			uni.navigateTo({
				url : "xgnicheng/xgnicheng?name="+this.useUser.name
			})
		},
		
		  // 修改手机号
		gotel : function(){
			uni.navigateTo({
				url : "xgtel/xgtel?tel="+this.tel
			})
		},
		
		    // 更换头像函数
		replacetx : function(){
			var that = this;
			uni.showActionSheet({
									
				itemList: ["从相册中选择头像","拍照"],
									success : function(res){
										var index = res.tapIndex;
										if(index == 0){
											uni.chooseImage({
											   count: 1, //默认9
											   sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
											   sourceType: ['album'], //从相册选择
											   success: function (res) {
												   console.log(res.tempFilePaths[0]);
												   // that.tximg = res.tempFilePaths[0];
												   console.log(res.tempFilePaths,res);
												   let thatRes=res;
												   
												   if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
												   	let uploadTask=uni.uploadFile({
												       url: that.url1+'/dinghai/Img/Img', //仅为示例，非真实的接口地址
												   	filePath:thatRes.tempFilePaths[0],
												       name: 'files',
												   	fileType:"image",
												       success: (uploadFileRes) => {
												           if(uploadFileRes.statusCode===200){
															   uni.showLoading({
															   	title:'正在修改头像',
																mask: true
															   })
												   			console.log(uploadFileRes.data)
															uni.request({
																		url: that.url1+"/dinghai/user/selectUser",
																		header:{
																			"Content-Type" : "application/x-www-form-urlencoded"
																		},
																		method:'POST',
																		data: {
																			uid:that.useUser.uid,
																			imgage:that.url1+'/image/'+uploadFileRes.data
																		},
																    success: (res) => {
																		console.log(res)
																		uni.showToast({
																			title:"修改成功"
																		})
																		let arr=that.useUser;
																		arr.imgage=that.url1+'/image/'+uploadFileRes.data;
																		that.setUser(arr)
																    },
																	fail : function(err){
																		console.log(err)
																		uni.showToast({
																			title:"您的网络打了个盹",
																			icon:"none"
																		})
																	}
																})
												   		}else{
												   			uni.showToast({
												   				title: '上传图片失败请重试',
												   				icon:"none"
												   			});
												   		}
												   	}
												   	});
												   	uploadTask.onProgressUpdate((res) => {
												   		console.log('上传进度' + res.progress);
												   		console.log('已经上传的数据长度' + res.totalBytesSent);
												   		console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
												   	});
												   }else{
												   	uni.showToast({
												   	icon:"none",
												   	title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
												   	})
												   }
											   }
											});
											console.log("从相册中选择头像");
										}else if(index == 1){
											uni.chooseImage({
											    count: 1,
											    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
											    sourceType: ['camera'], //相机
											    success: function (res) {
													console.log(res.tempFilePaths[0]);
													let thatRes=res;
													if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
														let uploadTask=uni.uploadFile({
													    url: that.url1+'/dinghai/Img/Img', //仅为示例，非真实的接口地址
														filePath:thatRes.tempFilePaths[0],
													    name: 'files',
														fileType:"image",
													    success: (uploadFileRes) => {
													        if(uploadFileRes.statusCode===200){
																uni.showLoading({
																	title:'正在修改头像',
																	mask: true
																})
																console.log(uploadFileRes.data)
																uni.request({
																			url: that.url1+"/dinghai/user/selectUser",
																			header:{
																				"Content-Type" : "application/x-www-form-urlencoded"
																			},
																			method:'POST',
																			data: {
																				uid:that.useUser.uid,
																				imgage:that.url1+'/image/'+uploadFileRes.data
																			},
																	    success: (res) => {
																			console.log(res)
																			uni.showToast({
																				title:"修改成功"
																			})
																			let arr=that.useUser;
																			arr.imgage=that.url1+'/image/'+uploadFileRes.data;
																			that.setUser(arr)
																	    },
																		fail : function(err){
																			console.log(err)
																			uni.showToast({
																				title:"您的网络打了个盹",
																				icon:"none"
																			})
																		}
																	})
															}else{
																uni.showToast({
																	title: '上传图片失败请重试',
																	icon:"none"
																});
															}
														}
														});
														uploadTask.onProgressUpdate((res) => {
															console.log('上传进度' + res.progress);
															console.log('已经上传的数据长度' + res.totalBytesSent);
															console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
														});
													}else{
														uni.showToast({
														icon:"none",
														title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
														})
													}
											    }
											});
										}
									}
			});
		},
		
		//更换头像函数结束
		
		   // 修改性别
	   xgxb : function(){
		   var that = this;
		   uni.showActionSheet({
		   	itemList: ["男","女"],
		   	success : function(res){
		   		var index = res.tapIndex;
				let usurl2=that.url+"/dinghai/user/update";
				uni.showLoading({
					title: '正在修改',
					mask: true
				});
				uni.request({
				    url: usurl2,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
				    data: {
						uid:that.useUser.uid,
						name:that.useUser.name,
						sex:index==0?'男':'女'
					},
				    success: (res) => {
						if(res.statusCode==200){
							uni.showToast({
								title:'修改成功'
							})
							that.useUser.sex=(index==0?'男':'女')
						}else{
							uni.showToast({
								title: '您的网络打了个盹！',
								icon:"none"
							});
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title: '您的网络打了个盹！',
							icon:"none"
							});
						}
					})
				}
			});
		},
	    
		// 修改地址
		xgdz : function(){
			uni.navigateTo({
				url : "../../dizhi/dizhi"
			})
		}
	}
}
